import React, { Component } from "react";
import { Row, Col, Divider } from "antd";

import IntlMessages from "../../../../components/utility/intlMessages";
import PrintTable from "../../../Tables/antTables/PrintTable.style";
import PrintTransaction, {
  PrintField,
  FieldCol
} from "../../../App/PrintTransaction";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import moment from "moment";
import { TotalField, Signature } from "../../../App/app.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

class Print extends Component {
  

  render() {
    const data = this.props.data ? this.props.data : [];
    const { loading } = this.props;

    let column = [
      {
        title: getLanguage({id: "no."}),
        dataIndex: "index_no"
      },
      {
        title: getLanguage({id: "invoiceNo"}),
        dataIndex: "purchase_invoice_no"
      },
      {
        title: getLanguage({id: "invoiceAmount"}),
        dataIndex: "purchase_invoice_amount",
        width: "20%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({id: "invoiceRate"}),
        dataIndex: "invoice_rate",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({id: "paidAmount"}),
        dataIndex: "paid_amount",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({id: "paymentAmount"}),
        dataIndex: "payment_amount",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({id: "discount"}),
        dataIndex: "sub_total_discount_amount",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({id: "sidebar.debitMemo"}),
        dataIndex: "sub_total_memo_amount",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({id: "outstanding"}),
        dataIndex: "outstanding",
        align: "right",
        render: text => <NumberFormat value={text} />
      }
    ];

    const leftSide = [
      {
        label: <IntlMessages id="payablePayments.location" />,
        description: data.location ? data.location.location_name : null
      },
      {
        label: <IntlMessages id="payablePayments.cashBank" />,
        description: data.cash_bank
          ? data.cash_bank.bank_account_number
            ? `${data.cash_bank.cash_bank_code} (${
            data.cash_bank.bank_account_number
            })`
            : data.cash_bank.cash_bank_code
          : null
      },
      {
        label: <IntlMessages id="payablePayments.cashFlowType" />,
        description: data.cash_flow_type
          ? data.cash_flow_type.description
          : null
      },
      {
        label: <IntlMessages id="currency" />,
        description: data.currency ? data.currency.currency_code : null
      }
    ];

    const rightSide = [
      {
        label: <IntlMessages id="payablePayments.dueDate" />,
        description: data.due_date
          ? moment(data.due_date, "YYYY-MM-DD HH:mm:ss").format("DD MMMM YYYY")
          : null
      },
      {
        label: <IntlMessages id="purchaseOrders.referenceNo" />,
        description: data.reference_no
      },
      {
        label: <IntlMessages id="purchaseOrders.issuerDest" />,
        description: data.bank_issuer
      },
      {
        label: <IntlMessages id="currencyFiscalRate" />,
        description:
          <>
            <NumberFormat value={data.currency_rate} />{'/'}<NumberFormat value={data.fiscal_rate} />
          </>
      }
    ];

    const total = [
      {
        label: <IntlMessages id="totalPaymentAmount" />,
        amount: <NumberFormat value={data.total_payment_amount} />,
        // parseAmount: true
      },
      {
        label: <IntlMessages id="totalMemoDP" />,
        amount: <NumberFormat value={data.total_memo_amount} />,
        // parseAmount: true
      },
      {
        // isDiscount: true,
        label: <IntlMessages id="totalDiscount" />,
        // discount: data.discount,
        amount: <NumberFormat value={data.total_discount_amount} />,
        // parseAmount: true
      },
      {
        label: <IntlMessages id="total" />,
        amount: <NumberFormat value={data.total_amount} />,
        // parseAmount: true
      }
    ];

    const signatureData = [
      {
        label: <IntlMessages id="createdBy" />
      },
      {
        label: <IntlMessages id="confirmedBy" />
      }
    ];
    return (
      <>
        <PrintTransaction
          className="print-transaction"
          componentRef={ref => (this.componentRef = ref)}
          title={<IntlMessages id="sidebar.payablePayment" />}
          subtitle={
            <>
              <Row>
                <Col>
                  <strong>{data.payable_payment_no}</strong>
                </Col>
              </Row>
              <Row style={{ marginTop: "10px" }}>
                <Col span={24}>
                  <PrintField
                    bold={true}
                    label={<IntlMessages id="date" />}
                    description={
                      data.transaction_date
                        ? moment(
                          data.transaction_date,
                          "YYYY-MM-DD HH:mm:ss"
                        ).format("DD MMMM YYYY, HH:mm:ss")
                        : null
                    }
                  />
                </Col>
              </Row>
            </>
          }
        >
          {/* Bagian atas (Subheader) */}
          <Row>
            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="sidebar.supplier" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={13}>
                  <strong>
                    {data.supplier ? data.supplier.supplier_name : null}
                  </strong>
                </Col>
              </Row>
            </Col>

            <Col span={12} style={{ fontSize: "12px" }}>
              <Row>
                <Col span={8}>
                  <IntlMessages id="note" />
                </Col>
                <Col span={1}>
                  <span>:</span>
                </Col>
                <Col span={15}>
                  <span>{data.note}</span>
                </Col>
              </Row>
            </Col>
          </Row>

          {/* Bagian list detail */}
          <Row style={{ marginTop: "24px" }}>
            <FieldCol leftSide={leftSide} rightSide={rightSide} />
          </Row>

          {/* Bagian Table */}
          <Row style={{ marginTop: "24px" }}>
            <Divider
              orientation="left"
              style={{
                fontSize: "12px",
                alignItems: "center",
                display: "flex"
              }}
            >
              <IntlMessages
                id="purchaseInvoices.tabs.item"
                style={{ marginTop: "-10" }}
              />
            </Divider>
            <Col span={24}>
              <PrintTable
                columns={column}
                dataSource={data.payable_payment_details}
                rowKey={row => row.id_payable_payment_detail}
                pagination={false}
              />
            </Col>
          </Row>

          <Row type="flex" style={{ marginTop: "24px" }}>
            <Col push={12} span={12}>
              <TotalField data={total} />
            </Col>
          </Row>

          <Row style={{ marginTop: "24px" }}>
            <Col span={24}>
              <Signature data={signatureData} />
            </Col>
          </Row>
        </PrintTransaction>

        <ReactToPrint
          trigger={() => (
            <ButtonPrint loading={loading} style={{ marginLeft: "5px" }} />
          )}
          content={() => this.componentRef}
          onBeforePrint={() => document.title = getFileName("payable_payment", data.payable_payment_no, true)}
          onAfterPrint={() => document.title = siteConfig.documentTitle}
        />
      </>
    );
  }
}

export default Print;
