import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import PayablePaymentUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";
import HistoryPayablePayment from "./history";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/payablePayment/actions";
import { Box } from "../../../App/app.style";
import { Spin, Tooltip } from "antd";
import Print from "./print";

const redirecTo = "/dashboard/financials/payable-payments";

class UpdatePayablePayment extends Component {
  state = {
    visible: false
  };

  componentDidMount() {
    const { editPayablePayment } = this.props;
    const { id } = this.props.match.params;
    editPayablePayment(id);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.payablePayment.error) {
      notification("error", this.props.payablePayment.error);
      this.props.clearError();
    }
    if (this.props.payablePayment.success) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.payablePayment.id_purchase_invoice
      // );
      //   this.props.history.replace(redirecTo);
      notification("success", this.props.payablePayment.success);
      this.props.clearSuccess();
    }
    if (this.props.payablePayment.warning) {
      // this.props.history.replace(
      //   redirecTo + "/update/" + this.props.payablePayment.id_purchase_invoice
      // );
      //   this.props.history.replace(redirecTo);
      notification("warning", this.props.payablePayment.warning);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visible } = this.state;
    visible
      ? this.setState({ visible: false })
      : this.setState({ visible: true });
  };

  handleSubmit = value => {
    const { updatePayablePayment } = this.props;
    const { id } = this.props.match.params;
    updatePayablePayment(id, value);
  };

  handleSubmitCancel = value => {
    const { cancelPayablePayment } = this.props;
    const { id } = this.props.match.params;

    cancelPayablePayment(id, value);
  };

  render() {
    // console.log(this.props);
    const {
      loading,
      loadingForeign,
      loadingSubmit,
      payablePayment,
      printData
    } = this.props.payablePayment;
    const modified = {...payablePayment, ...printData}

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="payablePayments.update.title" />}
            marginBottom={true}
          >
            <Tooltip placement="top" title="Info">
              <Button
                style={{
                  fontSize: "24px",
                  border: 0
                  // color: theme.palette.warning
                }}
                icon="issues-close"
                onClick={() => this.handleModalHistory()}
                loading={loading}
              >
                {/* <IntlMessages id="itemRequests.history" /> */}
              </Button>
            </Tooltip>

            <Print loading={loading} data={modified} />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirecTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <HistoryPayablePayment
            visible={this.state.visible}
            handleModalHistory={this.handleModalHistory}
            description={payablePayment ? payablePayment.description : null}
          />

          {loading && loadingForeign && !loadingSubmit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <PayablePaymentUpdateForm
              submit={this.handleSubmit}
              payablePayment={this.props.payablePayment}
              params={this.props.match.params}
              handleSubmitCancel={this.handleSubmitCancel}
              auth={this.props.auth}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    payablePayment: state.payablePayment,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdatePayablePayment);
